import React, { useState } from "react"
import { post } from 'axios'
import moment from 'moment'
import mtz from 'moment-timezone'

import 'moment/locale/ru'

moment.locale('ru')

const App = () => {
  const [file, setFile] = useState(null)
  const handleChange = e => {
    const files = e.target.files
    const data = []
    let items
    for (let i = 0; i < files.length; i++) {
      items = files[i]
      data.push({
        items,
        clientID:'5e48fa7a55f0b81e382728b4'
      })
    }
    setFile(data)
    console.log('object', e.target.files)
  }
  const handleClick = () => {
    const datas = new FormData()
    file.map(item => {
      datas.append('file', item.items)
    })
    datas.append('clientID', '5e48fa7a55f0b81e382728b4')
    post(`http://localhost:4000/api/attachments`, datas)
      .then(({ data }) => { console.log('data', data) })
    
  }
  window.filezzz = file
  /**
   * <h1>Начало работы!</h1>
      <input type="file" multiple onChange={e => handleChange(e)} />
      <button onClick={ handleClick }>ЗАГРУЗИТЬ</button>
   */
  const now = moment()
  const auto = moment('2020-03-15 08:00:00', 'YYYY-MM-DD HH:mm:ss')
  //const auto = moment('2020-02-21 08:00:00', 'LTS', true)
  //const autoF = auto.format('YYYY-MM-DD HH:mm:ss')
	return (
		<div className='App'>
      <span>Старт приложения {auto.endOf('hour').fromNow()}!</span>
		</div>
	)
}

export default App
